'use client';

import * as React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Loader2, Send } from 'lucide-react';

// Definición de constantes para temas y cargos.
const TemasConsulta = [
  'SEGUIMIENTO DE EXPEDIENTE',
  'CONSULTA POR SUBSANACIÓN DE OBSERVACIONES CON OFICIO',
  'CONSULTA PARA APERTURA DE BOTICA',
  'CONSULTA DE LLENADO DE FORMATOS TUPA',
  'CONSULTA DE LLENADO DE FORMATOS NO TUPA',
  'CONSULTA TÉCNICA DE CROQUIS DEL EE.FF',
  'CONSULTA DEL OBSERVATORIO DE PRECIOS',
  'CONSULTA POR OFICIO DE COMUNICACIÓN',
  'CONSULTA EN QUÉ ESTADO SE ENCUENTRA MI EXPEDIENTE',
  'OTROS'
];
const Cargos = ['Propietario', 'Representante Legal', 'Director Técnico', 'Q.F. Asistente', 'Otros'];

// Esquema de validación del formulario con Zod.
const formSchema = z.object({
  nombreEstablecimiento: z.string().min(3, "El nombre es requerido."),
  codigoEstablecimiento: z.string().optional(),
  ruc: z.string().optional(),
  emailEstablecimiento: z.string().email("Debe ser un correo válido."),
  cargo: z.enum(Cargos, { required_error: "Debe seleccionar un cargo." }),
  tema: z.enum(TemasConsulta, { required_error: "Debe seleccionar un tema." }),
  descripcion: z.string().min(10, "La descripción debe tener al menos 10 caracteres."),
});

export default function NewQueryForm({ onNewQuery, onQuerySuccess }) {
  const [isLoading, setIsLoading] = React.useState(false);

  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      nombreEstablecimiento: "",
      codigoEstablecimiento: "",
      ruc: "",
      emailEstablecimiento: "",
      cargo: undefined,
      tema: undefined,
      descripcion: "",
    },
  });

  async function onSubmit(values) {
    setIsLoading(true);
    const success = await onNewQuery(values);
    setIsLoading(false);
    if (success) {
      form.reset(); // Limpia el formulario
      onQuerySuccess(true); // Notifica al padre para cambiar de pestaña
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="p-2 space-y-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-x-12 gap-y-8">
          {/* Columna Izquierda: Datos del Establecimiento */}
          <div className="space-y-8">
            <FormField control={form.control} name="nombreEstablecimiento" render={({ field }) => ( <FormItem><FormLabel className="text-lg">Nombre del Establecimiento</FormLabel><FormControl><Input placeholder="Ej: Botica Salud Total" {...field} className="text-lg p-6"/></FormControl><FormMessage /></FormItem> )} />
            <FormField control={form.control} name="ruc" render={({ field }) => ( <FormItem><FormLabel className="text-lg">RUC (Opcional)</FormLabel><FormControl><Input placeholder="Ej: 20123456789" {...field} className="text-lg p-6"/></FormControl><FormMessage /></FormItem> )} />
             <FormField control={form.control} name="codigoEstablecimiento" render={({ field }) => ( <FormItem><FormLabel className="text-lg">Código de Establecimiento (Opcional)</FormLabel><FormControl><Input placeholder="Ej: 12345" {...field} className="text-lg p-6"/></FormControl><FormMessage /></FormItem> )} />
          </div>
          
          {/* Columna Derecha: Detalles de la Consulta */}
          <div className="space-y-8">
            <FormField control={form.control} name="emailEstablecimiento" render={({ field }) => ( <FormItem><FormLabel className="text-lg">Email del Establecimiento</FormLabel><FormControl><Input type="email" placeholder="contacto@saludtotal.com" {...field} className="text-lg p-6"/></FormControl><FormMessage /></FormItem> )} />
            <FormField control={form.control} name="cargo" render={({ field }) => ( <FormItem><FormLabel className="text-lg">Cargo que ocupa</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}><FormControl><SelectTrigger className="text-lg p-6"><SelectValue placeholder="Seleccione su cargo" /></SelectTrigger></FormControl><SelectContent>{Cargos.map(cargo => ( <SelectItem key={cargo} value={cargo} className="text-lg">{cargo}</SelectItem> ))}</SelectContent></Select><FormMessage /></FormItem> )} />
            <FormField control={form.control} name="tema" render={({ field }) => ( <FormItem><FormLabel className="text-lg">Tema de la Consulta</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}><FormControl><SelectTrigger className="text-lg p-6"><SelectValue placeholder="Seleccione un tema" /></SelectTrigger></FormControl><SelectContent>{TemasConsulta.map(tema => ( <SelectItem key={tema} value={tema} className="text-lg">{tema}</SelectItem> ))}</SelectContent></Select><FormMessage /></FormItem> )} />
          </div>
        </div>

        {/* Campo de Descripción (ocupa todo el ancho) */}
        <FormField control={form.control} name="descripcion" render={({ field }) => ( <FormItem><FormLabel className="text-lg">Descripción Detallada</FormLabel><FormControl><Textarea placeholder="Describa su consulta con el mayor detalle posible..." className="resize-y text-lg p-6" rows={6} {...field} /></FormControl><FormDescription className="text-base">Sea claro y conciso para una respuesta más rápida.</FormDescription><FormMessage /></FormItem> )} />
        
        {/* Botón de Envío */}
        <div className="flex justify-end pt-4">
          <Button 
            type="submit" 
            disabled={isLoading} 
            className="bg-[#1E3A8A] text-white text-lg font-bold py-7 px-10 rounded-lg hover:bg-[#162B65] transition-transform transform hover:scale-105 shadow-lg"
          >
            {isLoading ? <Loader2 className="mr-3 h-6 w-6 animate-spin" /> : <Send className="mr-3 h-6 w-6" />} 
            Enviar Consulta
          </Button>
        </div>
      </form>
    </Form>
  );
}
