'use client';

import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Checkbox } from '@/components/ui/checkbox';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Loader2, UserPlus } from 'lucide-react';

// Esquema de validación para el formulario de registro.
const formSchema = z.object({
  nombres: z.string().min(3, "El nombre debe tener al menos 3 caracteres."),
  apellidos: z.string().min(3, "El apellido debe tener al menos 3 caracteres."),
  email: z.string().email("Debe ser un correo electrónico válido."),
  celular: z.string().regex(/^[0-9]{9}$/, "El número de celular debe tener 9 dígitos."),
  autorizaNotificaciones: z.boolean().default(true),
});

export default function RegisterForm({ dni, onSubmit, isLoading }) {
  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      nombres: '',
      apellidos: '',
      email: '',
      celular: '',
      autorizaNotificaciones: true,
    },
  });

  return (
    // Tarjeta blanca y limpia para el formulario de registro
    <Card className="w-full max-w-3xl mx-auto shadow-2xl rounded-2xl border-t-8 border-[#1E3A8A]">
      <CardHeader className="text-center p-8">
        <UserPlus className="mx-auto h-12 w-12 text-[#1E3A8A]" />
        <CardTitle className="text-3xl font-bold text-gray-800 mt-4">
          Complete su Registro
        </CardTitle>
        <CardDescription className="text-lg text-gray-600 mt-2">
          Su DNI no fue encontrado. Por favor, complete sus datos para crear una cuenta.
        </CardDescription>
      </CardHeader>
      <CardContent className="p-8">
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
            {/* Campo DNI (deshabilitado) */}
            <div className="space-y-2">
              <FormLabel className="text-lg text-gray-700">DNI</FormLabel>
              <Input value={dni} disabled className="bg-gray-100 text-lg p-6 rounded-md" />
            </div>

            {/* Campos de Nombre y Apellidos en una fila */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              <FormField
                control={form.control}
                name="nombres"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="text-lg text-gray-700">Nombres</FormLabel>
                    <FormControl>
                      <Input placeholder="Escriba sus nombres" {...field} className="text-lg p-6 rounded-md"/>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="apellidos"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="text-lg text-gray-700">Apellidos</FormLabel>
                    <FormControl>
                      <Input placeholder="Escriba sus apellidos" {...field} className="text-lg p-6 rounded-md"/>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            {/* Campos de Correo y Celular en una fila */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              <FormField
                control={form.control}
                name="email"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="text-lg text-gray-700">Correo Electrónico</FormLabel>
                    <FormControl>
                      <Input type="email" placeholder="su.correo@ejemplo.com" {...field} className="text-lg p-6 rounded-md"/>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="celular"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="text-lg text-gray-700">Celular</FormLabel>
                    <FormControl>
                      <Input placeholder="987654321" {...field} className="text-lg p-6 rounded-md"/>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            {/* Checkbox de autorización */}
            <FormField
              control={form.control}
              name="autorizaNotificaciones"
              render={({ field }) => (
                <FormItem className="flex flex-row items-center space-x-3 space-y-0 rounded-lg border bg-gray-50 p-6 shadow-sm">
                  <FormControl>
                    <Checkbox checked={field.value} onCheckedChange={field.onChange} id="autorizacion" className="h-5 w-5"/>
                  </FormControl>
                  <div className="space-y-1 leading-none">
                    <FormLabel htmlFor="autorizacion" className="text-base text-gray-700 cursor-pointer">
                      Autorizo recibir notificaciones por correo y/o SMS sobre el estado de mis consultas.
                    </FormLabel>
                  </div>
                </FormItem>
              )}
            />

            {/* Botón de Envío */}
            <Button 
              type="submit" 
              disabled={isLoading} 
              className="w-full bg-[#1E3A8A] text-white text-lg font-bold p-7 rounded-md hover:bg-[#162B65] transition-transform transform hover:scale-105 shadow-lg"
            >
              {isLoading ? (
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
              ) : (
                <UserPlus className="mr-2 h-5 w-5" />
              )}
              Crear mi Cuenta
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
}
