'use client';

import * as React from 'react';
import DniForm from '@/components/auth/dni-form';
import RegisterForm from '@/components/auth/register-form';
import { UserDashboard } from '@/components/dashboard/user-dashboard';
import { validateDni, registerUser, createNewQuery } from '@/lib/actions';
import { useFirestore } from '@/firebase';
import { Toaster, toast } from 'sonner';

export default function HomePage() {
  const firestore = useFirestore();
  const [user, setUser] = React.useState(null);
  const [dni, setDni] = React.useState('');
  const [showRegister, setShowRegister] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);

  const handleDniSubmit = async (dni) => {
    setIsLoading(true);
    toast.loading('Validando DNI...');
    try {
      const foundUser = await validateDni(firestore, dni);
      if (foundUser) {
        setUser(foundUser);
        setDni(foundUser.dni);
        toast.success('¡Bienvenido de nuevo!');
      } else {
        setDni(dni);
        setShowRegister(true);
        toast.info('DNI no encontrado. Por favor, regístrese.');
      }
    } catch (error) {
      console.error("Error al validar DNI:", error);
      toast.error('Error al validar el DNI. Inténtelo de nuevo.');
    }
    setIsLoading(false);
  };

  const handleRegisterSubmit = async (formData) => {
    setIsLoading(true);
    toast.loading('Registrando usuario...');
    try {
      const newUser = await registerUser(firestore, formData, dni);
      setUser(newUser);
      setShowRegister(false);
      toast.success('¡Registro exitoso! Ya puede realizar consultas.');
    } catch (error) {
      console.error("Error al registrar usuario:", error);
      toast.error(error.message || 'Error al registrar el usuario. Inténtelo de nuevo.');
    }
    setIsLoading(false);
  };

  const handleNewQuery = async (queryData) => {
    if (!user) {
      toast.error("Debe iniciar sesión para crear una consulta.");
      return false;
    }
    toast.loading("Enviando su consulta...");
    try {
      await createNewQuery(firestore, queryData, user.id);
      toast.success("Su consulta ha sido registrada exitosamente.");
      return true; // Indica éxito
    } catch (error) {
      console.error("Error al crear la consulta:", error);
      toast.error("Hubo un error al registrar su consulta.");
      return false; // Indica fallo
    }
  };

  const renderContent = () => {
    if (isLoading && !user && !showRegister) {
      return <DniForm onSubmit={handleDniSubmit} isLoading={true} />;
    }

    if (user) {
      return <UserDashboard user={user} onNewQuery={handleNewQuery} />;
    }

    if (showRegister) {
      return <RegisterForm dni={dni} onSubmit={handleRegisterSubmit} isLoading={isLoading} />;
    }

    return <DniForm onSubmit={handleDniSubmit} isLoading={false} />;
  };

  return (
    <div className="w-full max-w-5xl mx-auto py-8">
      <Toaster richColors position="top-center" />
      {renderContent()}
    </div>
  );
}
