'use client';

import * as React from 'react';
import { collection, query, where, onSnapshot, orderBy } from "firebase/firestore";
import { useFirestore } from '@/firebase';
import { getStatusStyles } from '@/lib/utils';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { MessageSquareX } from 'lucide-react';

// Componente "esqueleto" para la tabla de historial mientras carga.
function HistoryTableSkeleton() {
  return (
    <div className="border rounded-lg p-4">
      <Skeleton className="h-8 w-full mb-4" />
      <div className="space-y-4">
        {[...Array(4)].map((_, i) => (
          <div key={i} className="flex justify-between items-center">
            <Skeleton className="h-6 w-1/4" />
            <Skeleton className="h-6 w-1/4" />
            <Skeleton className="h-6 w-1/4" />
            <Skeleton className="h-8 w-24 rounded-full" />
          </div>
        ))}
      </div>
    </div>
  );
}

// Componente para mostrar cuando no hay consultas.
function NoQueriesState() {
  return (
    <div className="text-center p-12 border-2 border-dashed rounded-lg bg-gray-50">
      <MessageSquareX className="mx-auto h-16 w-16 text-gray-400" />
      <h3 className="mt-4 text-2xl font-semibold text-gray-700">No hay consultas todavía</h3>
      <p className="mt-2 text-base text-gray-500">
        Cuando realice una nueva consulta, aparecerá aquí. ¡Empiece en la pestaña "Nueva Consulta"!
      </p>
    </div>
  );
}

export default function QueryHistory({ userId }) {
  const firestore = useFirestore();
  const [queries, setQueries] = React.useState([]);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    if (!firestore || !userId) return;

    const q = query(
      collection(firestore, "queries"),
      where("userId", "==", userId),
      orderBy("fechaCreacion", "desc")
    );

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const userQueries = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setQueries(userQueries);
      setLoading(false);
    }, (error) => {
      console.error("Error al obtener el historial de consultas: ", error);
      setLoading(false);
    });

    return () => unsubscribe();
  }, [firestore, userId]);

  if (loading) {
    return <HistoryTableSkeleton />;
  }

  if (queries.length === 0) {
    return <NoQueriesState />;
  }

  return (
    <div className="border rounded-xl overflow-hidden shadow-sm">
      <Table className="min-w-full divide-y divide-gray-200">
        <TableHeader className="bg-gray-50">
          <TableRow>
            <TableHead className="px-6 py-4 text-left text-sm font-semibold text-gray-600 uppercase tracking-wider">N° Expediente</TableHead>
            <TableHead className="px-6 py-4 text-left text-sm font-semibold text-gray-600 uppercase tracking-wider">Tema</TableHead>
            <TableHead className="px-6 py-4 text-left text-sm font-semibold text-gray-600 uppercase tracking-wider">Fecha</TableHead>
            <TableHead className="px-6 py-4 text-center text-sm font-semibold text-gray-600 uppercase tracking-wider">Estado</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody className="bg-white divide-y divide-gray-200">
          {queries.map((query) => (
            <TableRow key={query.id} className="hover:bg-gray-50 transition-colors">
              <TableCell className="px-6 py-5 whitespace-nowrap text-lg font-medium text-gray-800">{query.expediente}</TableCell>
              <TableCell className="px-6 py-5 whitespace-nowrap text-base text-gray-600">{query.tema}</TableCell>
              <TableCell className="px-6 py-5 whitespace-nowrap text-base text-gray-600">{new Date(query.fechaCreacion).toLocaleDateString('es-ES', { year: 'numeric', month: 'long', day: 'numeric' })}</TableCell>
              <TableCell className="px-6 py-5 whitespace-nowrap text-center">
                <Badge style={getStatusStyles(query.status)} className="px-4 py-2 text-sm font-bold rounded-full shadow-md">
                  {query.status}
                </Badge>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
