'use client';

import * as React from 'react';
import NewQueryForm from './new-query-form';
import QueryHistory from './query-history';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { User as UserIcon, List, PlusCircle } from 'lucide-react';

export function UserDashboard({ user, onNewQuery }) {
  const [activeTab, setActiveTab] = React.useState("history");

  // Esta función se llama cuando una nueva consulta se crea con éxito.
  const handleQuerySuccess = (success) => {
    if (success) {
      // Cambia a la pestaña de historial para que el usuario vea la nueva consulta.
      setActiveTab("history");
    }
  };

  return (
    // Contenedor principal más ancho y centrado
    <div className="w-full max-w-6xl mx-auto space-y-8 my-8">
      
      {/* Encabezado de Bienvenida Rediseñado */}
      <div className="text-center space-y-4">
        <div className="inline-block bg-gray-100 p-4 rounded-full border-4 border-white shadow-md">
          <UserIcon className="h-16 w-16 text-gray-500" />
        </div>
        <div>
          <h1 className="text-4xl font-bold text-gray-800">
            Bienvenido, <span className="text-[#1E3A8A]">{user.nombres} {user.apellidos}</span>
          </h1>
          <p className="text-lg text-gray-600 mt-2">
            Desde aquí puede gestionar sus consultas técnicas de manera fácil y rápida.
          </p>
        </div>
      </div>

      {/* Pestañas con Estilo Mejorado */}
      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-2 h-auto p-2 bg-gray-200 rounded-lg gap-2">
          <TabsTrigger 
            value="history" 
            className="text-lg font-semibold py-4 rounded-md transition-all duration-300 data-[state=active]:bg-[#1E3A8A] data-[state=active]:text-white data-[state=active]:shadow-lg"
          >
            <List className="mr-2 h-5 w-5" />
            Historial de Consultas
          </TabsTrigger>
          <TabsTrigger 
            value="new" 
            className="text-lg font-semibold py-4 rounded-md transition-all duration-300 data-[state=active]:bg-[#1E3A8A] data-[state=active]:text-white data-[state=active]:shadow-lg"
          >
            <PlusCircle className="mr-2 h-5 w-5" />
            Nueva Consulta
          </TabsTrigger>
        </TabsList>
        
        {/* Contenido de las Pestañas dentro de una Tarjeta */}
        <div className="mt-6 border-t-4 border-gray-200 pt-6">
          <TabsContent value="history">
            <Card className="shadow-xl rounded-2xl">
              <CardHeader>
                <CardTitle className="text-2xl font-bold text-gray-800">Su Historial de Consultas</CardTitle>
                <CardDescription className="text-base text-gray-600">A continuación se muestran todas las consultas que ha realizado.</CardDescription>
              </CardHeader>
              <CardContent>
                <QueryHistory userId={user.id} />
              </CardContent>
            </Card>
          </TabsContent>
          <TabsContent value="new">
            <Card className="shadow-xl rounded-2xl">
              <CardHeader>
                <CardTitle className="text-2xl font-bold text-gray-800">Registrar Nueva Consulta</CardTitle>
                <CardDescription className="text-base text-gray-600">Complete el formulario para enviar una nueva consulta técnica.</CardDescription>
              </CardHeader>
              <CardContent>
                <NewQueryForm onNewQuery={onNewQuery} onQuerySuccess={handleQuerySuccess} />
              </CardContent>
            </Card>
          </TabsContent>
        </div>
      </Tabs>
    </div>
  );
}
