'use client';

import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Card, CardContent } from '@/components/ui/card';
import { Loader2, ShieldCheck } from 'lucide-react';
import Image from 'next/image';

// Esquema de validación para el formulario de DNI.
const formSchema = z.object({
  dni: z.string().regex(/^[0-9]{8}$/, "El DNI debe contener 8 dígitos numéricos."),
});

export default function DniForm({ onSubmit, isLoading }) {
  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: { dni: '' },
  });

  // Llama a la función `onSubmit` del componente padre con el valor del DNI.
  function handleFormSubmit(values) {
    onSubmit(values.dni);
  }

  return (
    <Card className="w-full max-w-lg mx-auto bg-[#1E3A8A] border-4 border-white shadow-2xl text-white rounded-2xl">
      <CardContent className="p-8 md:p-12">
        <div className="flex flex-col items-center text-center mb-8">
          <Image
            src="https://i.ibb.co/jZ0wJZt/escudo-peru.png"
            alt="Escudo de Perú"
            width={80}
            height={80}
            className="mb-4"
          />
          <h1 className="text-3xl font-bold tracking-tight">Portal de Consultas</h1>
          <p className="text-gray-300 mt-2 max-w-sm">
            Ingrese su DNI para registrar una nueva consulta o ver el estado de sus consultas existentes.
          </p>
        </div>

        <Form {...form}>
          <form onSubmit={form.handleSubmit(handleFormSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="dni"
              render={({ field }) => (
                <FormItem>
                  <FormLabel className="text-lg">Número de DNI</FormLabel>
                  <FormControl>
                    {/* Input con fondo blanco y texto oscuro */}
                    <Input 
                      placeholder="Ej: 12345678" 
                      {...field} 
                      className="bg-white text-gray-900 text-lg p-6 rounded-md focus:ring-2 focus:ring-offset-2 focus:ring-offset-[#1E3A8A] focus:ring-white transition"
                    />
                  </FormControl>
                  <FormMessage className="text-amber-300" />
                </FormItem>
              )}
            />
            <Button 
              type="submit" 
              disabled={isLoading} 
              className="w-full bg-white text-[#1E3A8A] text-lg font-bold p-7 rounded-md hover:bg-gray-200 transition-transform transform hover:scale-105 shadow-lg"
            >
              {isLoading ? (
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
              ) : (
                <ShieldCheck className="mr-2 h-5 w-5" />
              )}
              Validar DNI
            </Button>
          </form>
        </Form>
        
        <div className="mt-8 text-center">
            <p className="text-xs text-gray-400 max-w-xs mx-auto">
                Toda la información proporcionada tiene carácter de declaración jurada y está sujeta a fiscalización posterior.
            </p>
        </div>
      </CardContent>
    </Card>
  );
}
