import { doc, getDoc, setDoc, addDoc, collection, runTransaction } from "firebase/firestore";

/**
 * Valida si un DNI ya está registrado en la base de datos.
 * @param {import("firebase/firestore").Firestore} db - La instancia de Firestore.
 * @param {string} dni - El DNI a validar.
 * @returns {Promise<import("./definitions").User | null>}
 */
export async function validateDni(db, dni) {
  const userRef = doc(db, "users", dni);
  const userSnap = await getDoc(userRef);
  if (userSnap.exists()) {
    return { id: userSnap.id, ...userSnap.data() };
  }
  return null;
}

/**
 * Registra un nuevo usuario en Firestore.
 * @param {import("firebase/firestore").Firestore} db - La instancia de Firestore.
 * @param {Omit<import("./definitions").User, 'id'>} newUser - Los datos del nuevo usuario.
 * @param {string} dni - El DNI del nuevo usuario, que se usará como ID.
 * @returns {Promise<import("./definitions").User>}
 */
export async function registerUser(db, newUser, dni) {
  const userRef = doc(db, "users", dni);
  const userSnap = await getDoc(userRef);

  if (userSnap.exists()) {
    throw new Error("Ya existe un usuario con este DNI.");
  }

  await setDoc(userRef, newUser);
  return { id: dni, ...newUser };
}

/**
 * Obtiene el siguiente número de secuencia para el expediente de una consulta.
 * Utiliza una transacción para asegurar que el número sea único.
 * @param {import("firebase/firestore").Firestore} db - La instancia de Firestore.
 * @returns {Promise<number>}
 */
async function getNextSequenceValue(db) {
  const counterRef = doc(db, "counters", "queryCounter");
  try {
    const newCurrent = await runTransaction(db, async (transaction) => {
      const counterSnap = await transaction.get(counterRef);
      if (!counterSnap.exists()) {
        // Si el contador no existe, lo crea con valor inicial 1.
        transaction.set(counterRef, { current: 1 });
        return 1;
      }
      const newCurrentValue = counterSnap.data().current + 1;
      transaction.update(counterRef, { current: newCurrentValue });
      return newCurrentValue;
    });
    return newCurrent;
  } catch (error) {
    console.error("Error en la transacción para obtener el siguiente valor de secuencia:", error);
    throw new Error("No se pudo generar el número de expediente.");
  }
}

/**
 * Crea una nueva consulta en la base de datos.
 * @param {import("firebase/firestore").Firestore} db - La instancia de Firestore.
 * @param {Omit<import("./definitions").Query, "id" | "userId" | "status" | "fechaCreacion" | "fechaActualizacion" | "expediente">} newQueryData - Los datos de la nueva consulta.
 * @param {string} userId - El DNI del usuario que crea la consulta.
 * @returns {Promise<import("firebase/firestore").DocumentReference>}
 */
export async function createNewQuery(db, newQueryData, userId) {
  const nextId = await getNextSequenceValue(db);
  const expediente = `EXP-${new Date().getFullYear()}-${String(nextId).padStart(6, '0')}`;

  const queryToSave = {
    ...newQueryData,
    userId: userId,
    status: "Pendiente",
    fechaCreacion: new Date().toISOString(),
    fechaActualizacion: new Date().toISOString(),
    expediente: expediente,
    observacion: "",
    asignadoA: "",
  };

  const queriesCollection = collection(db, "queries");
  const docRef = await addDoc(queriesCollection, queryToSave);
  return docRef;
}
